import React, { useRef, useEffect, useState } from "react";
import * as d3 from "d3";
import { saveSvgAsPng } from 'save-svg-as-png';
import Papa from 'papaparse';
import { createTheme, ThemeProvider, CssBaseline, Switch, FormControlLabel, Button, AppBar, Toolbar, Typography, Grid, TextField } from '@mui/material';
import Editor from "@monaco-editor/react";

const TimelineChart = () => {

  const initialCSV = `Release,Phase,Start,End
R4,ST,2024-04-01,2024-05-01
R4,SIT,2024-05-02,2024-06-01
R4,STAGING,2024-06-02,2024-07-01
R4,UAT,2024-07-02,2024-07-20
R4,PRODUCTION,2024-07-02,2024-07-15
R5,ST,2024-08-01,2024-09-01
R5,SIT,2024-09-02,2024-10-01
R5,STAGING,2024-10-02,2024-11-01
R5,PRODUCTION,2024-11-02,2024-11-15
R6,ST,2024-12-01,2025-01-01
R6,SIT,2025-01-02,2025-02-01
R6,STAGING,2025-02-02,2025-03-01
R6,PRODUCTION,2025-03-02,2025-03-15`;

  const [data, setData] = useState(null);
  const [title, setTitle] = useState("Timeline");
  const [darkMode, setDarkMode] = useState(true);
  const [fileContent, setFileContent] = useState(initialCSV);
  const [fileType, setFileType] = useState('csv');
  const [interval, setInterval] = useState(3); // Default interval for d3.timeWeek.every(...)
  const [yAxisLabel, setYAxisLabel] = useState("Release - Phase");
  const [showValues, setShowValues] = useState(false); // State for showing phase values
  const svgRef = useRef();


  

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    if (file.name.endsWith('.json')) {
      setFileType('json');
      reader.onload = (e) => {
        const jsonData = JSON.parse(e.target.result);
        setData(jsonData);
        setFileContent(JSON.stringify(jsonData, null, 2));
      };
      reader.readAsText(file);
    } else if (file.name.endsWith('.csv')) {
      setFileType('csv');
      reader.onload = (e) => {
        const csvContent = e.target.result;
        setFileContent(csvContent);
        parseCSVToJSON(csvContent);
      };
      reader.readAsText(file);
    }
  };

  const parseCSVToJSON = (csvContent) => {
    Papa.parse(csvContent, {
      header: true,
      dynamicTyping: true,
      complete: (results) => {
        const releasesMap = new Map();
        results.data.forEach((row) => {
          if (row.Release && row.Phase && row.Start && row.End) {
            if (!releasesMap.has(row.Release)) {
              releasesMap.set(row.Release, []);
            }
            releasesMap.get(row.Release).push({
              name: row.Phase,
              start: row.Start,
              end: row.End
            });
          }
        });

        const releases = Array.from(releasesMap, ([name, phases]) => ({
          name,
          phases
        }));

        const csvData = { releases };
        setData(csvData);
      }
    });
  };


  const getClipboardData = async () => {
    try {
      const text = await navigator.clipboard.readText();
      if (text) {
        setFileContent(text);
        parseCSVToJSON(text);
      }
    } catch (error) {
      console.error('Failed to read clipboard contents: ', error);
    }
  };

  useEffect(() => {
    parseCSVToJSON(initialCSV); // Parse initial CSV data on load

    const urlParams = new URLSearchParams(window.location.search);
    const clipboardParam = urlParams.get('c');
    if (clipboardParam === 'csv') {
      getClipboardData();
    }
  }, []);

  

  const handleEditorChange = (value) => {
    setFileContent(value);
    if (fileType === 'json') {
      try {
        const updatedData = JSON.parse(value);
        setData(updatedData);
      } catch (error) {
        console.error("Invalid JSON format:", error);
      }
    } else if (fileType === 'csv') {
      parseCSVToJSON(value);
    }
  };

  useEffect(() => {
    
    if (data) {
      const svg = d3.select(svgRef.current);
      svg.selectAll("*").remove();

      const margin = { top: 20, right: 20, bottom: 50, left: 150 }, // Increased left margin for y-axis label
        width = 1400 - margin.left - margin.right,
        height = 500 - margin.top - margin.bottom;

      const parseTime = d3.timeParse("%Y-%m-%d");
      const x = d3.scaleTime().range([0, width]);
      const y = d3.scaleBand().range([height, 0]).padding(0.1);

      const g = svg.append("g")
               .attr("transform", `translate(${margin.left + 20},${margin.top + 0})`);

      const phases = [];
      data.releases.forEach((release) => {
        release.phases.forEach((phase) => {
          phases.push({
            release: release.name,
            phase: phase.name,
            start: parseTime(phase.start),
            end: parseTime(phase.end),
          });
        });
      });

      x.domain([d3.min(phases, (d) => d.start), d3.max(phases, (d) => d.end)]);
      y.domain(phases.map((d) => `${d.release} - ${d.phase}`));

      g.append("g")
        .attr("class", "axis axis--x")
        .attr("transform", `translate(0,${height})`)
        .call(d3.axisBottom(x).ticks(d3.timeWeek.every(interval)).tickFormat(d3.timeFormat("%b %d")));

      g.append("g").attr("class", "axis axis--y").call(d3.axisLeft(y));

      g.append("g")
        .attr("class", "grid")
        .attr("transform", `translate(0,${height})`)
        .call(d3.axisBottom(x).tickSize(-height).tickFormat(""))
        .selectAll("line")
        .attr("stroke", darkMode ? "#444" : "#ccc");

      g.append("g")
        .attr("class", "grid")
        .call(d3.axisLeft(y).tickSize(-width).tickFormat(""))
        .selectAll("line")
        .attr("stroke", darkMode ? "#444" : "#ccc");

      const color = d3.scaleOrdinal(d3.schemeCategory10);

      const tooltip = d3.select("body").append("div")
        .attr("class", "tooltip")
        .style("position", "absolute")
        .style("background-color", darkMode ? "#333" : "white")
        .style("color", darkMode ? "white" : "black")
        .style("padding", "5px")
        .style("border", "1px solid #ccc")
        .style("border-radius", "4px")
        .style("pointer-events", "none")
        .style("opacity", 0);

      const bars = g.selectAll(".bar")
        .data(phases)
        .enter()
        .append("rect")
        .attr("class", "bar")
        .attr("x", (d) => x(d.start))
        .attr("y", (d) => y(`${d.release} - ${d.phase}`))
        .attr("width", (d) => x(d.end) - x(d.start))
        .attr("height", y.bandwidth())
        .attr("fill", (d, i) => color(i))
        .on("mouseover", (event, d) => {
          tooltip.transition().duration(200).style("opacity", .9);
          tooltip.html(`<strong>${d.phase}</strong><br>Start: ${d3.timeFormat("%Y-%m-%d")(d.start)}<br>End: ${d3.timeFormat("%Y-%m-%d")(d.end)}`)
            .style("left", (event.pageX + 5) + "px")
            .style("top", (event.pageY - 28) + "px");
        })
        .on("mouseout", () => {
          tooltip.transition().duration(500).style("opacity", 0);
        });

      if (showValues) {
        g.selectAll(".label")
          .data(phases)
          .enter()
          .append("text")
          .attr("class", "label")
          .attr("x", (d) => x(d.start) + (x(d.end) - x(d.start)) / 2)
          .attr("y", (d) => y(`${d.release} - ${d.phase}`) + y.bandwidth() / 2)
          .attr("dy", ".35em")
          .attr("text-anchor", "middle")
          .attr("fill", darkMode ? "#fff" : "#000")
          .text((d) => d.phase);
      }

      g.append("text")
        .attr("x", width / 2)
        .attr("y", -margin.top / 1.5 + 10) // Adjusted top margin
        .attr("text-anchor", "middle")
        .attr("font-size", "16px")
        .attr("font-weight", "bold")
        .text(title)
        .attr("fill", darkMode ? "#fff" : "#000");

      // Add y-axis label
      g.append("text")
        .attr("transform", "rotate(-90)")
        .attr("y", -margin.left)
        .attr("x", -height / 2)
        .attr("dy", "1em")
        .style("text-anchor", "middle")
        .text(yAxisLabel)
        .attr("fill", darkMode ? "#fff" : "#000");
    }
  }, [data, title, darkMode, interval, yAxisLabel, showValues]);

  const handleExport = () => {
    const svg = svgRef.current;
    saveSvgAsPng(svg, 'chart.png', { backgroundColor: 'white' });
  };

  const theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
    },
  });

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Timeline Chart
          </Typography>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} sm={2}>
              <TextField
                label="Chart Title"
                variant="outlined"
                fullWidth
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                inputProps={{ maxLength: 100 }}
              />
            </Grid>
            <Grid item xs={12} sm={2}>
              <TextField
                label="Interval (weeks)"
                variant="outlined"
                fullWidth
                value={interval}
                onChange={(e) => setInterval(Number(e.target.value))}
                inputProps={{ min: 1 }}
              />
            </Grid>
            <Grid item xs={12} sm={2}>
              <TextField
                label="Y-Axis Label"
                variant="outlined"
                fullWidth
                value={yAxisLabel}
                onChange={(e) => setYAxisLabel(e.target.value)}
                inputProps={{ maxLength: 100 }}
              />
            </Grid>
            <Grid item xs={12} sm={2}>
              <FormControlLabel
                control={<Switch checked={showValues} onChange={() => setShowValues(!showValues)} />}
                label="Show Values"
              />
            </Grid>
            <Grid item xs={12} sm={2}>
              <FormControlLabel
                control={<Switch checked={darkMode} onChange={() => setDarkMode(!darkMode)} />}
                label="Dark Mode"
              />
            </Grid>
            <Grid item xs={12} sm={2} sx={{ textAlign: 'right' }}>
              <input
                type="file"
                onChange={handleFileUpload}
                style={{ display: "none" }}
                id="upload-json"
              />
              <label htmlFor="upload-json">
                <Button component="span" variant="contained">
                  Upload 
                </Button>
              </label>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <div className="p-4">
        {data && (
          <>
            <Button variant="contained" onClick={handleExport} className="mt-4 bg-yellow-500 text-white">
              Export as PNG
            </Button>
            <svg ref={svgRef} width="1440" height="540" className="mt-4"></svg>
          </>
        )}
        {fileContent && (
          <div className="mt-4">
            <Editor
              height="400px"
              language={fileType === 'json' ? 'json' : 'plaintext'}
              value={fileContent}
              onChange={handleEditorChange}
              theme={darkMode ? "vs-dark" : "light"}
            />
          </div>
        )}
      </div>
    </ThemeProvider>
  );
};

export default TimelineChart;